import { useState, useEffect } from 'react';
import './TraitCard.css'

const TraitCard = () => {
    // const [extroversionNum, setExtroversionNum] = useState((localStorage.getItem("extroversion").slice((0, -1)))) 
    console.log(localStorage.getItem("extroversionNum"));
    console.log(localStorage.getItem("agreeablenessNum"));
    console.log(localStorage.getItem("conscientiousnessNum"));
    console.log(localStorage.getItem("neuroticismNum"));
    console.log(localStorage.getItem("opennessToExperienceNum"));
    console.log(localStorage.getItem("mentalHealthNum"));
    return (
    
    <div className='suggestions-container'>
      
      <h1 className="header">Based on your quiz score - here are a few recommendations</h1>
      {parseInt(localStorage.getItem("extroversionNum")) > 80 ? <div className="suggestions"><p>Hanging around with people is good, but think about taking some time for self-improvement!</p></div> : null}
      {parseInt(localStorage.getItem("extroversionNum")) <= 40 ? <div className="suggestions"><p>It is important to socialize with people, even if it’s tiring or you don’t enjoy it very much.</p></div> : null}
      {parseInt(localStorage.getItem("agreeablenessNum")) > 80 ? <div className="suggestions"><p>It may be difficult, but try talking to others about your problems - more often than not it can help you!</p></div> : null}
      {parseInt(localStorage.getItem("conscientiousnessNum")) < 60 && parseInt(localStorage.getItem("conscientiousnessNum")) > 40 ? <div className="suggestions"><p>Set attainable goals to reach (for mental health and in general), and don’t push yourself too hard. People with high conscientiousness tend to be ambitious and high-achieving, but it can border on perfectionism, which leads to high stress or burnout. To avoid this, take regular breaks from work and enjoy short-term gratifications</p></div> : null}
      {parseInt(localStorage.getItem("conscientiousnessNum")) > 80 ? <div className="suggestions"><p>Having big goals for the future is a very good thing to have, but try to avoid perfectionism. Trying your best is important, but perfectionism at the cost of high stress and/or burnout is not healthy. A good way to prevent this is to take breaks every now and then and live a little.</p></div> : null}
      {parseInt(localStorage.getItem("conscientiousnessNum")) <= 40 ? <div className="suggestions"><p>A good way to follow through with big goals is to break them down into smaller, more manageable goals. You will find that this will help time management and the achievability of long term big goals.</p></div> : null}
      {parseInt(localStorage.getItem("neuroticismNum")) > 30 ? <div className="suggestions"><p>When you feel sad or depressed, or are feeling down, meditation and deep breathing exercises can help. If this continues, professional help is advised.</p></div> : null}
      {parseInt(localStorage.getItem("opennessToExperienceNum")) < 40 ? <div className="suggestions"><p>You may dislike change, or simply like things that are consistent and familiar, but it is important to sometimes get out of your comfort zone and try something new.</p></div> : null}
      {parseInt(localStorage.getItem("mentalHealthNum")) < 40 ? <div className="suggestions"><p>If you are feeling depressed or not wanting to do things you previously enjoyed, professional help is advised</p></div> : null}
      {parseInt(localStorage.getItem("mentalHealthNum")) >= 50 ? <div className="suggestions"><p>Mental Health isn't constant and while yours may be good, it could always change. Always look for ways to improve yourself!</p></div> : null}

    </div>
  );
}
 
export default TraitCard;   